body{
    margin: 0;
}
.main{
    background-color: burlywood;
}
.first{
    list-style-type: none;
    overflow: hidden;
    height: 8vh;
}
.navlink{
    color: black;
    float: left;
    padding: 1%;
    text-decoration: none;
    transition-duration: 1.5s;
}
.navlink:hover{
    color: black;
    font-size: 2.5vh;
    text-shadow: 2px 2px 3px white;
}